import {ScreenWrapper, ScrollableTabView, UIFlashList} from '@true/ui';
import React, {memo, useCallback, useMemo, useState} from 'react';
import {ChannelThumbnail} from '@/screens/ChannelScreen/ChannelThumbnail';
import {
  requestAllChannels,
  requestJoinedChannels,
} from '@/store/channels/function';
import {
  getChannels,
  getChannelsByQuery,
  useChannelsByQuery,
} from '@/store/channels';
import {normalizeStringForSearch, styled, useMe} from '@true/core';
import {EUserRole} from '@/utils/helper';
import {CustomHeader} from '@/components/CustomHeader';
import {navigateToCreateChannelScreen} from '@/utils';
import {IFilterParams} from '@/screens/SearchScreen/type';
import {EmptySearchView} from '@/components/EmptyView';
import {IMAGE_EMPTY_SEARCH} from '@true/asset';

export const ChannelScreen = memo(function () {
  const me = useMe();
  const [params, setParams] = useState<IFilterParams>({});
  const [search, setSearch] = useState('');
  const renderItem = useCallback(
    (item: any, index?: number, isLast?: boolean) => {
      return <ChannelThumbnail id={item} />;
    },
    [],
  );
  const renderItemFlashList = useCallback(({item}: any) => {
    return <ChannelThumbnail id={item} />;
  }, []);

  const onChangeText = useCallback(
    async (q: string) => {
      setSearch(q);
    },
    [params],
  );

  const onSetData = useCallback(
    (prevData: string[]) => {
      return (prevData || []).filter(item =>
        normalizeStringForSearch(
          getChannels(item as string)?.name || '',
        ).includes(search),
      );
    },
    [search],
  );
  const channelIds = useMemo(() => {
    const ids = getChannelsByQuery('all') || [];
    return onSetData(ids);
  }, [search, onSetData]);
  const renderContent = useMemo(() => {
    if (me && Number(me.role) === EUserRole.ADMIN) {
      return (
        <ScrollableTabView.Provider
          params={{}}
          renderItem={renderItem}
          getItemByQuery={getChannelsByQuery}
          useItemByQuery={useChannelsByQuery}
          tabs={[
            {
              title: 'Joined channels',
              content: (p: any) => (
                <ScrollableTabView.List
                  {...p}
                  itemHeight={210}
                  requestFn={requestJoinedChannels}
                  tab={'joined'}
                  numColumns={2}
                  setData={onSetData}
                />
              ),
            },
            {
              title: 'All channels',
              content: (p: any) => (
                <ScrollableTabView.List
                  {...p}
                  itemHeight={210}
                  requestFn={requestAllChannels}
                  numColumns={2}
                  setData={onSetData}
                />
              ),
            },
          ]}>
          <ScrollableTabView.TabBar />
          <SContentWrapper>
            <ScrollableTabView.Content />
          </SContentWrapper>
        </ScrollableTabView.Provider>
      );
    } else
      return (
        <SContentWrapper>
          <UIFlashList
            data={channelIds}
            renderItem={renderItemFlashList}
            numColumns={2}
            estimatedItemSize={210}
            ListEmptyComponent={
              <EmptySearchView
                title={'No channel was found'}
                image={IMAGE_EMPTY_SEARCH}
              />
            }
          />
        </SContentWrapper>
      );
  }, [me, search, channelIds, renderItem, renderItemFlashList]);

  return (
    <ScreenWrapper isGrey={false}>
      <CustomHeader
        onCreate={navigateToCreateChannelScreen}
        title={'Channel'}
        isSearch
        onChangeText={onChangeText}
      />
      {renderContent}
    </ScreenWrapper>
  );
});
const SContentWrapper = styled.View`
  flex: 1;
  margin-left: 12px;
`;
export default ChannelScreen;
