import {Fonts, getUserByQuery, styled} from '@true/core';
import React, {memo, useCallback} from 'react';
import {useChannels} from '@/store/channels';
import {navigateToDetailChannelScreen} from '@/utils';
import FastImage from 'react-native-fast-image';
import {globalStyles} from '@/utils/globalStyle';
import {Dimensions, View} from 'react-native';
import {IC_OUTLINED_PUBLIC} from '@true/asset';
import {IMAGE_THUMBNAIL_CHANNEL} from '@true/asset';

interface ChannelThumbnailProps {
  id: number;
}

export const ChannelThumbnail = memo(function (props: ChannelThumbnailProps) {
  const {id} = props;
  const channel = useChannels(id);
  const onPress = useCallback(() => {
    navigateToDetailChannelScreen({
      id: id,
      path: channel?.path || '',
    });
  }, []);

  return (
    <SContainer onPress={onPress}>
      <SCoverImage
        source={
          channel?.image ? {uri: channel?.image} : IMAGE_THUMBNAIL_CHANNEL
        }
        resizeMode={'cover'}
      />
      <SIconChannel>
        <SImage source={IC_OUTLINED_PUBLIC} />
      </SIconChannel>
      <SWrapperView>
        <View>
          <STextTitle numberOfLines={2} style={globalStyles.mt4}>
            {channel?.name}
          </STextTitle>
          <STextGrey style={globalStyles.mt4}>
            {channel?.metatype === 'company' ? 'Company-wide' : 'Group'}
          </STextGrey>
        </View>
        <STextGreyBold style={globalStyles.mt8}>
          {channel?.metatype === 'company'
            ? getUserByQuery('allIds').length
            : channel?.followers.length}{' '}
          members
        </STextGreyBold>
      </SWrapperView>
    </SContainer>
  );
});
const widthScreen = Dimensions.get('window').width;
const SContainer = styled.TouchableOpacity`
  background-color: white;
  border-radius: 8px;
  align-items: center;
  margin-top: 8px;
  width: ${(widthScreen - 16) / 2 - 16}px;
  padding-bottom: 8px;
  border: 1px solid ${p => p.theme.neutral5};
  height: 210px;
`;
const SCoverImage = styled(FastImage)`
  width: 100%;
  height: 100px;
  max-height: 100px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  background-color: #1c1ca3;
`;
const SWrapperView = styled.View`
  flex: 1;
  align-items: center;
  justify-content: space-between;
`;
const STextTitle = styled.Text`
  color: ${p => p.theme.neutral1};
  text-align: center;
  font-family: ${Fonts.Regular};
  font-size: 16px;

  font-weight: 500;
  width: 149px;
  line-height: 24px; /* 150% */
`;
const STextGrey = styled.Text`
  color: ${p => p.theme.neutral2};
  font-family: ${Fonts.Regular};
  font-size: 12px;

  font-weight: 400;
  line-height: 16px; /* 133.333% */
  text-align: center;
`;
const STextGreyBold = styled(STextGrey)`
  font-weight: 500;
`;
const SIconChannel = styled.View`
  width: 40px;
  height: 40px;
  background-color: ${p => p.theme.primaryColor};
  border-radius: 8px;
  border: 2.22222px solid #ffffff;
  margin-top: -32px;
  align-items: center;
  justify-content: center;
`;
const SImage = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: #fff;
`;
